import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { localStoragedecryp } from "../../../../../components/helpers/function";
class ExportTransaksiPembelianPdf extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let total_berat = 0;
    let total_berat_nota = 0;
    let total_harga_nota = 0;
    let total_harga = 0;
    let total_harga_rata = 0;
    let total_barang = 0;

    this.props.data.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        total_berat = total_berat + parseFloat(item.berat);
        total_berat_nota = total_berat_nota + parseFloat(item.berat_nota);
        total_harga_nota = total_harga_nota + parseFloat(item.harga_nota);
        total_harga = total_harga + parseFloat(item.harga);
        total_harga_rata = total_harga_rata + parseFloat(item.harga_rata);
      });
      total_barang = total_barang + barang.detail.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Transaksi Pembelian"
          sheet="Laporan Transaksi Pembelian"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="12"> Laporan Transaksi Pembelian </th>
            </tr>
            <tr>
              <th colSpan="12"> Periode </th>
            </tr>
            <tr>
              <th colSpan="12">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="12"></th>
            </tr>
            <tr>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                NO BELI
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                KODE SALES
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                KODE MEMBER
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                NAMA MEMBER
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                NAMA BARANG
              </td>
              {/* <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                NO HP
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                ALAMT CUSTOMER
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                KODE GUDANG
              </td> */}
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                KODE DEPT
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                BERAT NOTA
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                BERAT
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                HARGA NOTA
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                HARGA BELI
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                HARGA RATA
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                KONDISI
              </td>
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((item, index) => (
              <>
                {item._id.pembayaran.map((header_detail, index) => (
                  <>
                    {/* <tr>
                      <td
                        style={{
                          textAlign: "center",
                         
                        }}
                      >
                        No Faktur Group
                      </td>
                      <td colSpan="11" style={{ border: "1px solid #000" }}>
                        {" "}
                        {header_detail.no_faktur_group}
                      </td>
                    </tr> */}
                    <tr>
                      <td colSpan="12">
                        {header_detail.jenis}{" "}
                        {parseInt(header_detail.jumlah_rp).toLocaleString(
                          "ID-id"
                        )}
                      </td>
                    </tr>
                  </>
                ))}

                {item.detail.map((row, index) => (
                  <tr>
                    <td>{row.no_faktur_beli}</td>
                    <td>{row.kode_sales}</td>
                    <td>{row.kode_member}</td>
                    <td>{row.nama_customer}</td>
                    <td>{row.nama_barang}</td>
                    {/* <td style={{ whiteSpace: "nowrap" }}>{row.no_hp}</td>
                    <td>{row.alamat_customer}</td>
                    <td>{row.kode_gudang}</td> */}
                    <td>{row.kode_dept}</td>
                    <td>{row.berat_nota}</td>
                    <td>{row.berat}</td>
                    <td>{row.harga_nota}</td>
                    <td>{row.harga}</td>
                    <td>{row.harga_rata}</td>
                    <td>{row.kondisi}</td>
                  </tr>
                ))}
                <tr>
                  <td
                    colSpan="6"
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",

                      textAlign: "center",
                    }}
                  >
                    Subtotal : {item.detail.length} Transaksi
                  </td>
                  <td
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                    }}
                  >
                    {" "}
                    {item.detail
                      .map((list) => list.berat_nota)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                    }}
                  >
                    {" "}
                    {item.detail
                      .map((list) => list.berat)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                    }}
                  >
                    {" "}
                    {item.detail
                      .map((list) => list.harga_nota)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                    }}
                  >
                    {" "}
                    {item.detail
                      .map((list) => list.harga)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                    }}
                  >
                    {" "}
                    {item.detail
                      .map((list) => list.harga_rata)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                    }}
                  >
                    {" "}
                  </td>
                </tr>
              </>
            ))}
          </tbody>
          <tfoot>
            <tr>
              <td
                colSpan="6"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",

                  textAlign: "center",
                }}
              >
                Grand Total : {total_barang} Transaksi
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                {total_berat_nota}
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                {total_berat}
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                {total_harga_nota}
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                {total_harga}
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                {total_harga_rata}
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                {" "}
              </td>
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default ExportTransaksiPembelianPdf;
