import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  InputDate,
} from "../../../../components/helpers/field";
import { getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiLaporanKeuangan from "../../../../Validasi/LaporanKeuangan/ValidasiLaporanKeuangan";
import ExportTransaksiPembelianPdf from "./exel/ExportTransaksiPembelianPdf";
import ExportTransaksiPembelian from "./pdf/ExportTransaksiPembelian"
class HeadFormLaporanPembelianBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    ExportTransaksiPembelian(this.props.LaporanTransaksiPembelian);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  render() {
    let databarang = [];
    let berat = 0;
    let ongkos = 0;
    let harga = 0;
    let harga_nota = 0;
    let harga_gram = 0;
    let berat_nota = 0;
    this.props.LaporanTransaksiPembelian.forEach((element) => {
      element.detail.forEach((detail) => {
        databarang.push(detail);
        berat_nota = berat_nota + detail.berat_nota;
        berat = berat + detail.berat;
        ongkos = ongkos + detail.ongkos;
        harga = harga + detail.harga;
        harga_nota = harga_nota + detail.harga_nota;
        harga_gram = harga_gram + detail.harga_gram;
      });
    });
    const columns = [
      {
        dataField: "no_faktur_beli",
        text: "No FB",
        footerAttrs: {
          colSpan: "4",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Transaksi : {databarang.length} </div>;
        },
      },
      {
        dataField: "kode_sales",
        text: "Kode Sales",
      },
      {
        dataField: "kode_member",
        text: "Kode Member",
      },
      {
        dataField: "nama_customer",
        text: "Nama Member",
      },
      {
        dataField: "nama_barang",
        text: "Nama Barang",
      },
      
     
      {
        dataField: "berat_nota",
        text: "Berat Nota",
        footer: (cell, row, num, index) => {
          return <div>{berat_nota.toFixed(3)} </div>;
        },
        formatter: (cell) => {
          return cell.toFixed(3);
        },
      },
      {
        dataField: "berat",
        text: "Berat",
        footer: (cell, row, num, index) => {
          return <div>{berat.toFixed(3)} </div>;
        },
        formatter: (cell) => {
          return cell.toFixed(3);
        },
      },
      {
        dataField: "harga_nota",
        text: "Harga Nota",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">{harga_nota.toLocaleString("ID-id")} </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("ID-id")}</div>
          );
        },
      },
    
      {
        dataField: "harga",
        text: "Harga/Gram",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga.toLocaleString("ID-id")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("ID-id")}</div>
          );
        },
      },
      {
        dataField: "kondisi",
        text: "Kondisi",
        footer : ''
      },
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
      <div className="row">
        <div className="col-lg-4">
          <Field
            name="tgl_awal"
            component={InputDate}
            label="Tanggal Dari"
            type="text"
            selected={this.state.tgl_awal}
            onChange={(date) => this.setStartDate(date)}
            placeholder="Masukan Tanggal Dari"
          />
        </div>

        <div className="col-lg-4">
          <Field
            name="tgl_akhir"
            component={InputDate}
            type="text"
            selected={this.state.tgl_akhir}
            onChange={(date) => this.setLastDate(date)}
            label="Tanggal Akhir"
            placeholder="Masukan Tanggal Akhir"
          />
        </div>

        <div className="col-lg-4">
          <div className="text-right">
            <label>&nbsp;</label>
            <button
              type="submit"
              value="PDF"
              name="type_btn"
              className="btn btn-primary btn-block"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Mencari
                </>
              ) : (
                "Cari Laporan"
              )}
            </button>
          </div>
          </div>
          
          <div className="col-lg-12">
            <Tabel
              keyField="kode_barcode"
              data={this.props.export ? databarang : []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Transaksi Pembelian Kosong"
            />
          </div>
        <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
          {/* <label htmlFor="">&nbsp;</label> */}
          <div className="row">
            <div className="col-lg-6">
              <button
                onClick={() => this.exportPdf()}
                className="btn btn-warning btn-block"
              >
                {" "}
                Export PDF{" "}
              </button>
            </div>
            <div className="col-lg-6">
              <ExportTransaksiPembelianPdf data={this.props.LaporanTransaksiPembelian} />
            </div>
          </div>
        </div>
      </div>
    </form>
    );
  }
}

HeadFormLaporanPembelianBarang = reduxForm({
  form: "HeadFormLaporanPembelianBarang",
  enableReinitialize: true,
  validate: ValidasiLaporanKeuangan,
})(HeadFormLaporanPembelianBarang);
// export default connect(null)(HeadFormLaporanPembelianBarang);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormLaporanPembelianBarang);
