import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday, localStoragedecryp } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const ExportTransaksiPembelian = (
  data = "",
  username = "",
  tanggal_dari = "",
  tanggal_sampai = ""
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = []
  let finalY = 30;
 


  doc.setFontSize(15);
  doc.text("LAPORAN TRANSAKSI PEMBELIAN", 14, 15);
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN TRANSAKSI PEMBELIAN",
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  let total_berat = 0;
  let total_berat_nota = 0;
  let total_harga_nota = 0;
  let total_harga = 0;
  let total_harga_rata = 0;
  let total_barang = 0;
  tableColumn = [
    // [
    //   {
    //     content: `No Faktur Group : ${item._id.no_faktur_group} \n${header_detail.jenis}  (${parseInt(header_detail.jumlah_rp).toLocaleString("ID-id")})`,
    //     colSpan: 13,
    //   },
    // ],
    [
      {
        content: `NO BELI`,
      },
      {
        content: `KODE SALES`,
      },
      {
        content: `KODE MEMNBER`,
      },
      {
        content: `NAMA MEMBER`,
      },
      {
        content: `NAMA BARANG`,
      },
      // {
      //   content: `NO HP`,
      // },
      // {
      //   content: `ALAMAT CUSTOMER`,
      // },
      // {
      //   content: `KODE GUDANG`,
      // },
      {
        content: `KODE DEPT`,
      },
      {
        content: `BERAT NOTA`,
      },
      {
        content: `BERAT`,
      },
      {
        content: `HARGA NOTA`,
      },
      {
        content: `HARGA BELI`,
      },
      {
        content: `HARGA RATA`,
      },
      {
        content: `KONDISI`,
      },
    ],
    ];
  data.forEach((item, index) => {
    let tot_berat = 0;
    let tot_berat_nota = 0;
    let tot_harga_nota = 0;
    let tot_harga = 0;
    let tot_harga_rata = 0;
  
  
    item._id.pembayaran.forEach((header_detail, index) => {
    // console.log(item._id.pembayaran[0].jenis);
      let pembayaran = [
        {
          content: `${header_detail.jenis}  (${parseInt(header_detail.jumlah_rp).toLocaleString("ID-id")})`,
          colSpan: 13,
        },
      ]
      tableRows.push(pembayaran)
    });
      
    item.detail.forEach((item, index) => {
      let rows = [
        item.no_faktur_beli,
        item.kode_sales,
        item.kode_member,
        item.nama_customer,
        item.nama_barang,
        // item.no_hp,
        // item.alamat_customer,
        // item.kode_gudang,
        item.kode_dept,
        item.berat_nota.toFixed(3),
        item.berat.toFixed(3),
        parseInt(item.harga_nota).toLocaleString("ID-id"),
        parseInt(item.harga).toLocaleString("ID-id"),
        parseInt(item.harga_rata).toLocaleString("ID-id"),
        item.kondisi,
      ];
      tot_berat = tot_berat + parseFloat(item.berat);
      tot_berat_nota = tot_berat_nota + parseFloat(item.berat_nota);
      tot_harga_nota = tot_harga_nota + parseFloat(item.harga_nota);
      tot_harga = tot_harga + parseFloat(item.harga);
      tot_harga_rata = tot_harga_rata + parseFloat(item.harga_rata);
      
      total_berat = total_berat + parseFloat(item.berat);
      total_berat_nota = total_berat_nota + parseFloat(item.berat_nota);
      total_harga_nota = total_harga_nota + parseFloat(item.harga_nota);
      total_harga = total_harga + parseFloat(item.harga);
      total_harga_rata = total_harga_rata + parseFloat(item.harga_rata);
     
      tableRows.push(rows);
    });
    total_barang = total_barang + item.detail.length;
    let total = [
      {
        content: `Subtotal : ${item.detail.length} Transaksi`,
        colSpan: 7,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content:`${tot_berat_nota.toFixed(3)}`,
        styles: {
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content:`${tot_berat.toFixed(3)}`,
        styles: {
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content:`${parseInt(tot_harga_nota).toLocaleString("ID-id")}`,
        styles: {
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content:`${parseInt(tot_harga).toLocaleString("ID-id")}`,
        styles: {
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content:`${parseInt(tot_harga_rata).toLocaleString("ID-id")}`,
        styles: {
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content:``,
        styles: {
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
    ];
    tableRows.push(total);

  });
  let grand = [
    {
      content: `Grand Total : ${total_barang} Transaksi`,
      colSpan: 7,
      styles: {
        halign: 'center',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content:`${total_berat_nota.toFixed(3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content:`${total_berat.toFixed(3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content:`${parseInt(total_harga_nota).toLocaleString("ID-id")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content:`${parseInt(total_harga).toLocaleString("ID-id")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content:`${parseInt(total_harga_rata).toLocaleString("ID-id")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content:``,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
  ];
  tableRows.push(grand);
  let printed = [
    {
      colSpan:9,
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    foot: footRows,
    startY:  finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      fillColor: "#E8E5E5",
      textColor: "#000",
    },
 
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  footRows = [];

  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  // doc.text(`User                           : ${JSON.parse(localStoragedecryp("userdata")).user_id}`, 14, finalY + 10);
  // doc.text(`Cetak                        : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN TRANSAKSI PEMBELIAN.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN TRANSAKSI PEMBELIAN</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default ExportTransaksiPembelian;
